<template>
<div class="navbar-wrapper">
    <nav class="navbar navbar-expand-lg navbar-dark bg-transparent">
        <div class="container">
            <router-link class="navbar-brand" to="/">Learning-App</router-link>
            <button class="navbar-toggler" id="nav-collapse" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <ul class="navbar-nav ms-auto mb-2 mb-lg-0">
                    <li class="nav-item">
                        <router-link class="nav-link" class-active="active" to="/">Home</router-link>
                    </li>
                    <li class="nav-item">
                        <router-link class="nav-link" class-active="active" to="/about">About</router-link>
                    </li>
                    <li class="nav-item">
                        <router-link class="nav-link" class-active="active" to="/courses">Courses</router-link>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
    <transition name="fade">
        <router-view />
    </transition>
</div>
</template>

<script>
export default {
    name: "Header",
    watch: {
          '$route' () {
                const elebtn = document.querySelector(".navbar-toggler");
                const element = document.querySelector(".navbar-collapse");
                let isShown = element.classList.contains("show");
                if(isShown){
                  elebtn.classList.add("collapsed");
                  element.classList.remove("show");
                }
            }
        }
}
</script>

<style scoped>
.fade-enter-active {
   animation: fadeIn 1s ease-in;
}
@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.8;
    }
    100% {
        opacity: 1;
    }
}
.fade-leave-active {
    animation: moveUp 0.3s ease-in;
}

@keyframes moveUp {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(-400px);
    }
}

</style>
